import React from 'react'
import { Switch, Route} from 'react-router-dom'
import Login from './Views/Login'
import Home from './Views/Home'
import Products from './Views/Products'
import Stock from './Views/Stock'
import Sales from './Views/Sales'
import Customers from './Views/Customers'
import Category from './Views/Category'
import AddProducts from './Views/AddProducts'
import AddCategory from './Views/AddCategory'
import AddCustomers from './Views/AddCustomers'
import AddSales from './Views/AddSales'
import AddStock from './Views/AddStock'


const Main = () => (
  <main>
    <Switch>
      <Route exact path='/' component={Home} />
      <Route exact path='/login' component={Login} />

      <Route exact path='/product' component={Products} />
      <Route exact path='/product/add' component={AddProducts} />

      <Route exact path='/stock' component={Stock} />
      <Route exact path='/stock/add' component={AddStock} />

      <Route exact path='/sale' component={Sales} />
      <Route exact path='/sale/add' component={AddSales} />

      <Route exact path='/category' component={Category} />
      <Route exact path='/category/add' component={AddCategory} />

      <Route exact path='/customer' component={Customers} />
      <Route exact path='/customer/add' component={AddCustomers} />


    </Switch>
  </main>
)

export default Main;